.page-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.content {
    flex: 1 0 auto;
    min-height: 100vh;
}

@media (max-width: 510px) {
    .page-container {
        min-height: 84vh;
    }

    .content {
        min-height: 84vh;
    }


}